import axios from "axios";
import { __sourceType,__next } from "../data/jx3box.json";
const { cms_types } = __sourceType;
const $next = axios.create({
    baseURL: process.env.NODE_ENV === "production" ? __next : "/",
});

// å‘é€ç»Ÿè®¡ï¼šåŒä¸€ä¸ªipçš„åŒä¸€ç¯‡æ–‡ç« çš„åŒä¸€ä¸ªåŠ¨ä½œ10åˆ†é’Ÿå†…ä¸é‡å¤ç»Ÿè®¡
function postStat(type, id, action = "views") {
    let api = cms_types.includes(type)
        ? "/api/summary-any/" + id
        : "/api/summary-any/" + type + "-" + id;
    return $next.get(api, {
        params: {
            type: type,
            actions: action,
        },
    });
}

// 30ç§’ç¼“å­˜
function getStat(type, id) {
    let api = cms_types.includes(type)
        ? "/api/summary-any/" + id + "/stat"
        : "/api/summary-any/" + type + "-" + id + "/stat";
    return $next.get(api);
}


// è·å–ç»Ÿè®¡
function getStatRank(type, action = "views", limit = 10, sort = "7days") {
    return $next.get("/api/summary/visit/rank", {
        params: {
            postType: type,
            postAction: action,
            pageSize: limit,
            sort: sort, //é»˜è®¤"yesterday", å¯é€‰å€¼ï¼štoday,7days,30days
        },
    });
}

export { getStat, postStat, getStatRank };
